export default {
  name: '',
  data() {
    return {
      mvReady: false,
      playing: false,
      showPlayIcon: true,
      timer: -1
    };
  },
  created() {},
  deactivated() {
    this.video.src = ''
    this.$destroy()
    // this.mvReady = false
  },
  mounted() {
    this.video = this.$refs.video
    console.log(this.$route.query)
    this.$nextTick(() => {

      // this.$refs.video.load()
    })
  },
  watch: {
    showPlayIcon(newShow) {
      if (newShow) {
        // setTimeout
      } else {}
    }
  },
  methods: {
    delayHiddenPlayIcon() {
      var timer = setTimeout(() => {
        this.showPlayIcon = false;
        this.timer = -1
        console.log('delayHiddenPlayIcon')
      }, 8000)
      this.timer = timer

    },
    toggleShowPlayIcon() {
      // console.log('toggleShowPlayIcon')

      if (this.showPlayIcon) {
        clearTimeout(this.timer)
        // console.log(this.timer.length)
        this.timer = -1;
        this.showPlayIcon = false
      } else {
        this.showPlayIcon = true
        this.delayHiddenPlayIcon()

      }

    },
    togglePlaying() {
      this.video.paused ? this.video.play() : this.video.pause()
    },
    onpause() {
      this.playing = false
    },
    onplay() {
      this.playing = true
    },
    onabort() {
      console.log('onabort');
    },
    onerror() {
      console.log('onerror');
    },
    onprogress() {
      // console.log(this.video.buffered.end(0));

    },
    onwaiting() {
      this.mvReady = false

    },
    oncanplay() {
      this.mvReady = true
      this.video.play()
      console.log('oncanplay')
      this.delayHiddenPlayIcon()
    },
  }
};




